body {
  font-family: $mainFont;
  color: $text-color;
  background-color: $main-bg-color;
  &.modal-open {
    overflow: hidden;
  }
}

img {
  display: block;
}

a {
  text-decoration: none;
}

address {
  font-style: normal;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
